import React,{ Component } from "react";
import axios from "axios";
import 'bootstrap/dist/css/bootstrap.min.css';
import $ from 'jquery';
import Popper from 'popper.js';
import 'bootstrap/dist/js/bootstrap.bundle.min';
import { BrowserRouter as Router, Route,Redirect,Link,NavLink} from "react-router-dom";

export default class Book extends Component {
  constructor(props) {
    super(props);
    this.count=1;
    this.loadBooks = this.loadBooks.bind(this);
    this.state = {
      books:[],
          token:undefined
    };
  }

  componentDidMount() {
    this.loadBooks();
  
  }
  
  
  
  async loadBooks()
  {
//     const username="admin";
//     const password="admin123"
//     const temp=await axios.post("http://localhost:8000/login/",{username,password})
    
//     if(temp.status==200){
// this.setState(()=>({
//   token:temp.data.token
// }))
//       console.log(this.state.token)
//     }
console.log(this.props.token)
     const AuthStr = 'Token '.concat(this.props.token);
    //console.log(AuthStr)
    const promise = await axios.get("https://lomira-shell.herokuapp.com/book",{ headers: { Authorization: AuthStr } });
    const status = promise.status;
  if(status===200)
    { 
          console.log(promise.data)
      this.setState((prevState)=>({
          books:promise.data.data,
      }));
   //console.log(this.state.books[0])
    }
  }

  render() {
    return(
      <div >

   <nav className="navbar navbar-expand-lg navbar-light bg-light mb-1">
  <a className="navbar-brand" href="#">Lomira</a>

  <div className="navbar-nav ml-auto">
  <ul className="navbar-nav ml-auto">
      <li className="nav-item ml-auto">
        <NavLink className=" btn btn-primary ml-3"   to="/" onClick={this.props.redirect} >Sign Out </NavLink>
      </li>
      </ul>
  </div>
</nav>
{this.state.books.length>0 ?
<div className="table-responsive">
      <table className="table table-striped  table-hover ">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">Customer Name</th>
          <th scope="col">Purchased Date</th>
          <th scope="col">Truck Number</th>
          <th scope="col">Gallons Pumped</th>
          <th scope="col">Gross Amount</th>
          <th scope="col">Price Per Gallon</th>
          <th scope="col">Net Amount</th>
        </tr>
      </thead>
      <tbody>  
       {this.state.books.map((table,index)=>
        <tr>
          <th scope="row">{index+1}</th>
            <td >{table.customer}</td>     
            <td >{table.purchased_date}</td>     
            <td >{table.truck_number}</td>     
            <td >{table.gallons_pumped}</td>     
            <td >{table.gross_amount}</td>     
            <td >{table.price_per_gallon}</td>     
            <td >{table.net_amount}</td>     
    
            </tr>
          
    
            ) }
        </tbody>
    </table>
    </div> :false
  }
</div>
      )
  }
}
