import React from "react"
import { BrowserRouter as Router, Route, Link,NavLink } from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';
import $ from 'jquery';
import Popper from 'popper.js';
import 'bootstrap/dist/js/bootstrap.bundle.min';
export default class Login extends React.Component{
constructor(props){
    super(props);
}


    render(){
        return(    
            
            <div>

            <nav className="navbar navbar-expand-lg navbar-light bg-light mb-1">
            <a className="navbar-brand" href="#">Lomira</a>
            <button className="navbar-toggler navbar-light " type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
         
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="nav navbar-nav ml-auto">
                <li className="nav-item ml-auto">
                  <NavLink className=" btn btn-secondary ml-auto" activeClassName="is-active" id="login"   to="/" exact  >Login In </NavLink>
                </li>
                <li className="nav-item ml-auto">
                <NavLink className=" btn btn-primary ml-3" activeClassName="is-active" id="signup"   to="/signup" exact  >Sign Up </NavLink>
              </li>
 
                </ul>
            </div>
          </nav>
             <div className="container">
            <form className="form-horizontal" onSubmit={
                (e)=>{
                    e.preventDefault();
                    //console.log(e.target.elements.email1.value)
                  this.props.sign(e.target.elements.username.value,e.target.elements.email1.value,e.target.elements.password1.value,e.target.elements.password2.value)       
               
            }
            }>
                <div className="row">
                    <div className="col-md-3"></div>
                    <div className="col-md-6">
                        <h2>Create a new account
                        </h2>
                        <hr/>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-3"></div>
                    <div className="col-md-6">
                    {this.props.username &&<p>{this.props.username}</p>}        
                    <div className="form-group has-danger">
                            <label className="sr-only" for="email">Username</label>
                            <div className="input-group mb-2 mr-sm-2 mb-sm-0">
                                <div className="input-group-addon" ><i className="fa fa-at"></i></div>
                                <input type="text" name="username" className="form-control" id="email"
                                       placeholder="username" required  autoFocus autoComplete="nope" />
                            </div>
                        </div>
                    </div>
                </div>
               
                <div className="row">
                    <div className="col-md-3"></div>
                    <div className="col-md-6">
                    {this.props.email &&<p>{this.props.email}</p>}
                        <div className="form-group has-danger">
                            <label className="sr-only" for="email">Email</label>
                            <div className="input-group mb-2 mr-sm-2 mb-sm-0">
                                <div className="input-group-addon" ><i className="fa fa-at"></i></div>
                                <input type="email" name="email1" className="form-control" id="email"
                                       placeholder="email" required  autoComplete="nope" />
                            </div>
                        </div>
                    </div>
                </div>

            <div className="row">
            <div className="col-md-3"></div>
            <div className="col-md-6">
            {this.props.password1 &&<p>{this.props.password1}</p>}
                <div className="form-group has-danger">
                    <label className="sr-only" for="email">Password</label>
                    <div className="input-group mb-2 mr-sm-2 mb-sm-0">
                        <div className="input-group-addon" ><i className="fa fa-at"></i></div>
                        <input type="password" name="password1" className="form-control" id="email"
                               placeholder="Password" required   autoComplete="nope" />
                    </div>
                </div>
            </div>
        </div>

                <div className="row">
                    <div className="col-md-3"></div>
                    <div className="col-md-6">
                    {this.props.password2 &&<p>{this.props.password2}</p>}
                        <div className="form-group">
                            <label className="sr-only" for="password">Confirm Password</label>
                            <div className="input-group mb-2 mr-sm-2 mb-sm-0">
                                <div className="input-group-addon" ><i className="fa fa-key"></i></div>
                                <input type="password" name="password2" className="form-control" id="password"
                                       placeholder="Confirm Password" required  autoComplete="new-password" />
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row" >
                <div className="col-md-3"></div>
                <div className="col-md-6">
                 <button type="submit" className="btn btn-primary">Sign Up</button>
               </div>
            </div>
            </form> 
        </div>      
        <style jsx>{`
        .is-active{
            font-weight: bold;
        }
       
        .min-width{ 
            overflow-x:auto 
        }
        ul.nav{
            display:inline-flex
        }
          





                `}</style>
           </div>
        )
    }
}