import React from "react"
import { BrowserRouter as Router, Route, Link,NavLink } from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';
import $ from 'jquery';
import Popper from 'popper.js';
import 'bootstrap/dist/js/bootstrap.bundle.min';

export default class Login extends React.Component{
constructor(props){
    super(props);
}



componentDidMount(){
    this.props.setsign();
    
}
    render(){
        return(    
            
            <div>
            <nav className="navbar navbar-expand-lg navbar-light bg-light mb-1">
            <a className="navbar-brand" href="#">Lomira</a>
          
            <div className="navbar-nav ml-auto">
            <ul className="nav navbar-nav ml-auto">
                <li className="nav-item ml-auto">
                  <NavLink className=" btn btn-secondary ml-auto" activeClassName="is-active" id="login"   to="/" exact  >Login In </NavLink>
                </li>
                <li className="nav-item ml-auto">
                <NavLink className=" btn btn-primary ml-3" activeClassName="is-active" id="signup"   to="/signup" exact  >Sign Up </NavLink>
              </li>
 
                </ul>
            </div>
          </nav>
            
            <div className="container">
          
            <form className="form-horizontal"
            onSubmit={(e)=>{
                e.preventDefault();
           //    console.log(e.target.elements.username.value)
                this.props.click(e.target.elements.username.value,e.target.elements.password.value)       
            }
            }
            >
                <div className="row">
                    <div className="col-md-3"></div>
                    <div className="col-md-6">
                        <h2>Please Login</h2>
                        <hr/>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-3"></div>
                    <div className="col-md-6">
                    {this.props.error &&<p>{this.props.error}</p>}    
                    <div className="form-group has-danger">
                            <label className="sr-only" for="email">Username</label>
                            <div className="input-group mb-2 mr-sm-2 mb-sm-0">
                                <div className="input-group-addon" ><i className="fa fa-at"></i></div>
                                <input type="text" name="username" className="form-control" id="email"
                                       placeholder="username" required autoFocus/>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-3">
                        <div className="form-control-feedback">
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-3"></div>
                    <div className="col-md-6">
                        <div className="form-group">
                            <label className="sr-only" for="password">Password</label>
                            <div className="input-group mb-2 mr-sm-2 mb-sm-0">
                                <div className="input-group-addon" ><i className="fa fa-key"></i></div>
                                <input type="password" name="password" className="form-control" id="password"
                                       placeholder="Password" required/>
                            </div>
                        </div>
                    </div>
                    
                    
                </div>
                <div className="row" >
                <div className="col-md-3"></div>
                <div className="col-md-6">
                <button type="submit" className="btn btn-primary"><i className="fa fa-sign-in"></i> Login</button>
                <a class="btn btn-link" href="/password/reset">Forgot Your Password?</a>
                </div>
            </div>
            </form> 
        </div>      
        <style jsx>{`
        .is-active{
            font-weight: bold;
        }
        
                `}</style>
           </div>
        )
    }
}